import { HttpService } from './../../../services/http-service';
import { ConfigProvider } from './../../../providers/config-provider';

import { ViewController } from 'ionic-angular';

import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, ModalController, Events, LoadingController, IonicApp } from 'ionic-angular';

import { AuthProvider, CountryProvider } from '../../../providers/providers';
import { StorageService, Session, NativeService } from '../../../services/services';

import { ActionSheetController } from 'ionic-angular/components/action-sheet/action-sheet-controller';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClient } from '@angular/common/http';
import { WEIXIN_APPID, WEIXIN_APPSECRET, LANGUAGES } from './../../../services/constants';
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {
  // The account fields for the login form.
  account: { country: string, mobilephone: string, password: string } = {
    country: "+86",
    mobilephone: "",
    password: ''
  };

  // Our translated text strings
  private loginErrorString: string;
 
  language: string;
  languages: any[] = LANGUAGES;
  private loginEnabled: boolean = false;

  private isShowWechat: boolean = true;
  constructor(public navCtrl: NavController,
    public modalCtrl: ModalController,
    public authProvider: AuthProvider,
    public translateService: TranslateService,
    public actionSheetCtrl: ActionSheetController,
    public session: Session,
    public events: Events,
    public http: HttpClient,
    private nativeService: NativeService,
    private storageService: StorageService,
    private loadingCtrl: LoadingController,
    public countryProvider: CountryProvider,
    public ionicApp: IonicApp,
    public ViewCtrl: ViewController,
    public configProvider: ConfigProvider,
    private httpService: HttpService 
  ) {
    this.validateInput();

    this.events.subscribe('Login2:success', (resp) => {
      this.loginSuccess(resp)
    })

    this.events.subscribe('extra-login-success', () => {
      this.closeThisWindow();
    })

    this.storageService.getItem("mobilephone").then((mobilephone) => {
      this.account.mobilephone = mobilephone;
    });
    this.storageService.getItem("country").then((country) => {
      if (country != null && country != undefined) {
        this.account.country = country;
      } else {
        // this.account.country = "+86";
        this.nativeService.getSimInfo().subscribe((info) => {
          if (info.countryCode) {
            this.account.country = '+' + this.countryProvider.query(info.countryCode)[0].number;
          }
        }, (err) => {
        })

      }
    });


  }

  ionViewDidLoad() {
    this.storageService.getItem("language").then((language) => {
      this.language = language;
    })

    // this.events.subscribe('wechat-auth', (resp) => {
    //   this.getWechatUserInfo(resp)
    // })
  }

  ionViewWillUnload(){
    this.events.unsubscribe('wechat-auth');
  }
  // Attempt to login in through our User service
  doLogin() {
    this.loginEnabled = false;
    this.LoginByMobileBefore()
  }

  /**
   * 验证输入
   */
  validateInput() { 
    if (this.account.mobilephone.length > 0 && this.account.password.length > 0) {
      this.loginEnabled = true;
    } else {
      this.loginEnabled = false;
    } 
  }

  /**
   * 登录前先进行验证码校验
   */
  LoginByMobileBefore() {
    let accountInfo = {
      "mobilePhone": this.account.country + " " + this.account.mobilephone,
      "password": this.account.password,
      "lang": this.session.language
    } 
    
    accountInfo['clientId'] = this.account.country + " " + this.account.mobilephone, 
    this.authProvider.sendSecurityCodeBefore('LoginByMobileBefore',accountInfo,'',()=>{
      this.loginEnabled = true;
    }) 
  }
 
  loginSuccess(resp) {
    this.storageService.setItem("mobilephone", this.account.mobilephone);
    this.storageService.setItem("country", this.account.country);
    this.closeThisWindow();
  }


  /**
   *快捷登录
   */
  doForgotPassword() {

    this.modalCtrl.create('ForgotPasswordPage', {
      mobile: this.account.mobilephone
    }).present();
    // this.navCtrl.push('ForgotPasswordPage', {
    //   mobile: this.account.mobilephone
    // });
  }

  /**
   * workflow账号登录
   */
  goWorkflowLogin() {
    // this.navCtrl.push('WfLoginPage');

    this.modalCtrl.create('WfLoginPage').present();
  }

  /**
   * 选择地区
   */
  doSelectCountry() {
    let selectModal = this.modalCtrl.create('SelectCountryPage', { isNeedAll: true });
    selectModal.onDidDismiss(item => {
      if (item) {
        this.account.country = "+" + item.number;
        this.storageService.setItem("country", this.account.country);
      }
    })
    selectModal.present();
  }

  /**
   * 注册
   */
  register() {
    // this.navCtrl.push('SignupPage');
    this.modalCtrl.create('SignupPage').present();
  }

  /**
   * 打开语言选择
   */
  doSelectLanguage() {
    let ActionSheetButtons = [];
    for (let item of LANGUAGES) {
      let button = {
        text: item.name,
        role: this.language === item.value ? 'destructive' : '',
        icon: this.language === item.value ? 'ios-checkmark' : '',
        handler: () => {
          this.language = item.value
          this._selectLanguage(item.value);
        }
      }
      ActionSheetButtons.push(button)
    }
    ActionSheetButtons.push({
      text: this.translateService.instant('APP_COMMON_CANCEL'),
      role: 'cancel',
      handler: () => {
      }
    })

    this.actionSheetCtrl.create({
      title: this.translateService.instant('LOGIN_LANGUAGE_SELECT'),
      buttons: ActionSheetButtons
    }).present();
  }

  /**选定语言 */
  _selectLanguage(lang: string) { 
    this.language = lang;  
    this.configProvider.changeLanguage(lang,true);
  }

  /**
   * 关闭当前页面
   */
  closeThisWindow(){
    this.ViewCtrl.dismiss();
    if (location.href.split('/').pop().indexOf('my-list') > -1) {
      this.events.publish('to-homemain')
    }
  }

  async WeChatAuth() { 
     const code = await this.authProvider.getWeChatCode() 
     this.nativeService.showLoading()
     this.authProvider.LoginByWechat(code).subscribe(res=>{
      this.authProvider.loginSuccessHandle(res);
      this.loginSuccess(res) 
     },err =>{
       let msg =  this.translateService.instant('LOGIN_ERROR') 
       if(err.errcode === 6008){
            this.session.unionId = err.result && err.result.unionId
            msg =  this.translateService.instant('LOGIN_ERROR_CODE_6008')
       }else if(err.errcode === 6006){
        msg =  this.translateService.instant('LOGIN_ERROR_CODE_6006') 
       }
       this.nativeService.alert(msg);
     },()=>{
      this.nativeService.hideLoading()
     })
  }

  getWechatUserInfo(response){
      alert('step 1 response:' + JSON.stringify(response))
      let grant_type = 'authorization_code';
      let code = response.code;
      let getTokenUrl = `https://api.weixin.qq.com/sns/oauth2/access_token?appid=${WEIXIN_APPID}&secret=${WEIXIN_APPSECRET}&code=${code}&grant_type=${grant_type}`;
      alert("getTokenUrl:" + getTokenUrl)

      this.httpService.get(getTokenUrl).subscribe(res=>{
      if(res.errcode == 0){
        let accessTokenResponse = res.result;
        var accessToken = accessTokenResponse.access_token;
        var openId = accessTokenResponse.openid;
        alert(JSON.stringify(accessTokenResponse));
        // 3. 获取用户信息
        let getUserUrl = `https://api.weixin.qq.com/sns/userinfo?access_token=${accessToken}&openid=${openId}&lang=zh_CN`;
        alert("getUserUrl:" + getUserUrl)
        this.httpService.get(getUserUrl).subscribe(userInfoResponse=> {

          alert(JSON.stringify(userInfoResponse))
          // openid    普通用户的标识，对当前开发者帐号唯一
          // nickname    普通用户昵称
          // sex    普通用户性别，1为男性，2为女性
          // province    普通用户个人资料填写的省份
          // city    普通用户个人资料填写的城市
          // country    国家，如中国为CN
          // headimgurl    用户头像，最后一个数值代表正方形头像大小（有0、46、64、96、132数值可选，0代表640*640正方形头像），用户没有头像时该项为空
          // privilege    用户特权信息，json数组，如微信沃卡用户为（chinaunicom）
          // unionid    用户统一标识。针对一个微信开放平台帐号下的应用，同一用户的unionid是唯一的。
          
        });
      }
      alert('http :' +JSON.stringify(res))
      })
  }
}



